<template>
  <div>
    <van-cell
      :title="title"
      :value="timeName"
      is-link
      @click="showSelect = true"
    />
    <van-action-sheet
      v-model:show="showSelect"
      :title="title"
      :actions="actions"
      cancel-text="取消"
      close-on-click-action
      @select="onSelect"
    />
    <van-calendar
      v-model:show="showCalendar"
      :title="title"
      :type="type"
      allow-same-day
      :min-date="minDate"
      :max-date="maxDate"
      :defaultDate="defaultDate"
      @confirm="confirmUpdateTime"
    />
  </div>
</template>

<script>
import { Calendar } from "vant";

export default {
  name: "viCalendar",
  components: { [Calendar.name]: Calendar },
  props: {
    // 范围传null默认不选中
    modelValue: {
      type: [String, Array],
    },
    title: {
      type: String,
      default: "时间",
    },
    type: {
      type: String,
      default: "range", // single单个时间，range时间范围
    },
    minDate: {
      type: Date,
      default: new Date("2023"),
    },
    maxDate: {
      type: Date,
      default: new Date().endOf("month"),
    },
    actions: {
      type: Array,
      default(props) {
        return props.type == "range" ? [
          { name: "近一周", value: [new Date().subtract(6, "days").startOf("day"), new Date().endOf("day")] },
          { name: "近三天", value: [new Date().subtract(2, "days").startOf("day"), new Date().endOf("day")] },
          { name: "近两天", value: [new Date().subtract(1, "days").startOf("day"), new Date().endOf("day")] },
          { name: "今天", value: [new Date().startOf("day"), new Date().endOf("day")] },
          { name: "自定义", value: "", color: "#07c160" }
        ] : props.type == "single" ? [
          { name: "前天", value: new Date().subtract(2, "days").startOf("day") },
          { name: "昨天", value: new Date().subtract(1, "days").startOf("day") },
          { name: "今天", value: new Date().startOf("day") },
          { name: "自定义", value: "", color: "#07c160" }
        ] : [];
      },
    }
  },
  data() {
    return {
      showSelect: false,
      showCalendar: false,
    };
  },
  computed: {
    selectTime: {
      get() {
        return this.modelValue;
      },
      set(val) {
        this.$emit("update:modelValue", val);
      },
    },
    defaultDate() {
      return this.type == "range" ?
        (this.selectTime ? this.selectTime.map((date) => new Date(date)) : [new Date(), new Date()]) :
        this.type == "single" ?
          (this.selectTime ? new Date(this.selectTime) : "") : "";
    },
    timeName() {
      return this.type == "range" ?
        (this.selectTime ? this.selectTime.join(" - ") : "") :
        this.type == "single" ? this.selectTime || "" : "";
    },
  },
  methods: {
    onSelect({ value }) {
      if (value) {
        this.confirmUpdateTime(value);
      } else {
        this.showCalendar = true;
      }
    },
    confirmUpdateTime(values) {
      if (this.type == "range") {
        const [start, end] = values;
        this.selectTime = [start.format("YYYY-MM-DD"), end.format("YYYY-MM-DD")];
        this.showCalendar = false;
      } else if (type == "single") {
        this.selectTime = values.format("YYYY-MM-DD");
      }
    },
  },
};
</script>

<style lang="less" scoped>

</style>
